import Vue from 'vue'
import Router from 'vue-router'
import vScroll from 'vue-scroll'
import Home from '@/components/Home'
import CV from '@/components/CV'

Vue.use(Router)
Vue.use(vScroll)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        nav: []
      }
    },
    {
      path: '/cv',
      name: 'CV',
      component: CV,
      meta: {
        nav: [
          {
            href: '#personal-details',
            tooltip: 'Personal Details',
            icon: 'fa-user'
          },
          {
            href: '#education',
            tooltip: 'Education',
            icon: 'fa-book'
          },
          {
            href: '#qualifications',
            tooltip: 'Qualifications',
            icon: 'fa-graduation-cap'
          },
          {
            href: '#key-experience',
            tooltip: 'Key Experience',
            icon: 'fa-desktop'
          },
          {
            href: '#key-skills',
            tooltip: 'Key Skills',
            icon: 'fa-laptop'
          },
          {
            href: '#employment-history',
            tooltip: 'Employment History',
            icon: 'fa-briefcase'
          },
          {
            href: 'javascript:window.print()',
            tooltip: 'Print',
            icon: 'fa-print'
          }
        ]
      }
    }
  ]
})



