<template>
    <div id="app" class="blue-grey lighten-2">
        <header>
            <div class="navbar-fixed">
                <nav data-min-height="64" data-max-height="500" data-min-line-height="1.5" data-min-brand-size="2.8">
                    <div class="container">
                        <router-link :to="{ name: 'Home' }" class="brand-logo">Phil Wright- Christie</router-link>
                        <ul id="nav-mobile" class="right hide-on-med-and-down">
                            <li v-for="item in $route.meta.nav">
                                <a :href="item.href" class="tooltipped" data-position="bottom" data-delay="50"
                                   :data-tooltip="item.tooltip">
                                    <i :class="item.icon" class="fa" aria-hidden="true"></i>
                                </a>
                            </li>
                        </ul>
                    </div>
                </nav>
            </div>
        </header>

        <main>
            <router-view></router-view>
        </main>

        <footer class="page-footer grey darken-1">
            <div class="footer-copyright">
                <div class="container">
                    &copy; 2017 Phil Wright- Christie
                    <a class="grey-text text-lighten-4 right" target="_blank" href="http://philwc.com">philwc.com</a>
                </div>
            </div>
        </footer>
    </div>
</template>

<script>
  export default {
    name: 'app'
  }

  if (window.matchMedia('screen').matches) {
    window.jQuery = window.$ = require('jquery')
    var $ = window.$
    require('font-awesome-webpack')
    require('materialize-loader')
    require('materialize-css')

    $(document).on('scroll', function () {
      var distanceY = window.pageYOffset || document.documentElement.scrollTop
      var $nav = $('nav')

      var minHeight = parseInt($nav.attr('data-min-height'))
      var maxHeight = parseInt($nav.attr('data-max-height'))
      var minBrandSize = parseFloat($nav.attr('data-min-brand-size'))
      var minLineHeight = parseFloat($nav.attr('data-min-line-height'))

      var newHeight = maxHeight - distanceY
      var newSize = newHeight / 100
      var newLineHeight = newHeight / 100

      if (newHeight < minHeight) {
        newHeight = minHeight
      }

      if (newSize < minBrandSize) {
        newSize = minBrandSize
      }

      if (newLineHeight < minLineHeight) {
        newLineHeight = minLineHeight
      }

      $nav.css('height', newHeight)
      $nav.find('.brand-logo').css({
        'font-size': newSize + 'em',
        'line-height': newLineHeight + 'em'
      })
    })

    $(document).ready(function () {
      var $nav = $('nav')
      var maxHeight = parseInt($nav.attr('data-max-height'))
      $nav.css('height', maxHeight)
      var newSize = maxHeight / 100

      $nav.find('.brand-logo').css({
        'font-size': newSize + 'em',
        'line-height': newSize + 'em'
      })

      $('.navbar-fixed').css('height', maxHeight + 50)
    })

    $(document).on('click', 'a', function (event) {
      var $this = $(this)
      var href = $this.attr('href')

      if (href.substring(0, 10) === 'javascript' || href.substring(0, 1) !== '#') {
        return
      }

      event.preventDefault()
      $('html, body').animate({
        scrollTop: $(href).offset().top - 75
      }, 500)
    })
  }
</script>

<style lang="scss" media="screen">
    @media print {
        body {
            font-family: Georgia, serif;
            background: none;
            color: black !important;
            width: 100%;
            margin: 0;
            float: none;
            font-size: 10pt !important;
        }
        a, a:visited {
            color: black !important;
            text-decoration: none;
        }
        header, footer {
            display: none !important;
        }
        main {
            flex: none;
        }
        .container, .row {
            width: 100% !important;
            margin: 0 !important;
            padding: 0 !important;
            float: none !important;
        }
        p, li, td {
            color: black !important;
        }
        h4 {
            font-size: 16pt !important;
            margin: 16pt 0 !important;
            color: black;
            line-height: 16pt !important;
        }
        h5 {
            font-size: 14pt !important;
            margin: 14pt 0 !important;
            color: black;
            line-height: 14pt !important;
        }

        #personal-details {
            a {
                color: black !important;
            }

            .card-action {
                border: none;
            }

            a[data-tooltip]:after {
                content: ' [ ' attr(data-tooltip) ' ' attr(href) ' ] \a';
                white-space: pre;
                font-style: italic;
                font-weight: lighter;
                text-decoration: none;
                color: black;
                text-transform: none;
            }

            i {
                color: black !important;
            }

            .card-content {
                .col {
                    width: 33% !important;
                }
            }
        }

        #key-experience, #key-skills, #qualifications, #education {
            .card-content {
                .col {
                    width: 50% !important;
                }
            }
        }

        #personal-details, #key-skills, #key-experience, #qualifications, #education, #employment-history {
            page-break-inside: avoid;
        }

        #employment-history {
            h5 {
                color: #222 !important;
            }
            li {
                list-style-position: outside;
                list-style-type: disc;
            }
            .card-panel {
                page-break-inside: avoid;
            }
        }

        .card, .card-panel, .card-content {
            border: none;
            box-shadow: none;
            margin: 0 !important;
            padding: 0 !important;
            color: black;
            .chip {
                color: black;
            }
        }

        table {
            td {
                width: 50% !important;
            }
        }

        .material-tooltip {
            display: none !important;
        }
    }

    @media screen {
        @import "~materialize-css/sass/components/mixins";
        @import "~materialize-css/sass/components/color";
        @import "~materialize-css/sass/components/variables";
        @import url('https://fonts.googleapis.com/icon?family=Material+Icons');

        $secondary-color: color("orange", "lighten-1") !default;

        nav {
            background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.9) 100%), url('./assets/img/bg.jpg');
            background-clip: content-box;
            background-size: cover;
            margin-bottom: 50px;
            transition: height 0.3s;

            ul {
                i {
                    height: 15px;
                }
            }
        }

        body {
            display: flex;
            min-height: 100vh;
            flex-direction: column;
        }

        main {
            flex: 1 0 auto;
        }
    }
</style>



