<template>
    <div class="container" v-if="data">
        <div class="row">
            <div class="col s12">
                <div class="row" id="personal-details">
                    <div class="col s12">
                        <div class="card blue-grey darken-1">
                            <div class="card-content white-text">
                                <span class="card-title orange-text"><h4><i class="fa fa-user" aria-hidden="true"></i> Personal Details</h4></span>
                                <div class="row">
                                    <div class="col s12 m4 l4">
                                        <h5>Name</h5>
                                        <p>{{data.personal.name}}</p>
                                    </div>
                                    <div class="col s12 m4 l4">
                                        <h5>Telephone</h5>
                                        <a :href="'tel:'+ data.personal.telephone">{{data.personal.telephone}}</a>
                                    </div>
                                    <div class="col s12 m4 l4">
                                        <h5>Date of Birth</h5>
                                        <p>{{data.personal.dateOfBirth}}</p>
                                    </div>
                                </div>

                                <div class="row">
                                    <div class="col s12 m4 l4">
                                        <h5>Email Address</h5>
                                        <a target="_blank" :href="'mailto:' + data.personal.email">{{data.personal.email}}</a>
                                    </div>
                                    <div class="col s12 m4 l4">
                                        <h5>Address</h5>
                                        <p>{{data.personal.address}}</p>
                                    </div>
                                    <div class="col s12 m4 l4">
                                        <h5>Website</h5>
                                        <a target="_blank" :href="data.personal.website">{{data.personal.website}}</a>
                                    </div>
                                </div>
                            </div>

                            <div class="card-action">
                                <div class="row">
                                    <div class="col s12">
                                        <a class="tooltipped" data-position="bottom" data-delay="50"
                                           data-tooltip="Github" target="_blank" :href="data.personal.github">
                                            <i class="orange-text fa fa-github" aria-hidden="true"></i>
                                        </a>
                                        <a class="tooltipped" data-position="bottom" data-delay="50"
                                           data-tooltip="Bitbucket" target="_blank"
                                           :href="data.personal.bitbucket">
                                            <i class="orange-text fa fa-bitbucket" aria-hidden="true"></i>
                                        </a>
                                        <a class="tooltipped" data-position="bottom" data-delay="50"
                                           data-tooltip="Twitter" target="_blank" :href="data.personal.twitter">
                                            <i class="orange-text fa fa-twitter" aria-hidden="true"></i>
                                        </a>
                                        <a class="tooltipped" data-position="bottom" data-delay="50"
                                           data-tooltip="StackOverflow" target="_blank"
                                           :href="data.personal.stackoverflow">
                                            <i class="orange-text fa fa-stack-overflow" aria-hidden="true"></i>
                                        </a>
                                        <a class="tooltipped" data-position="bottom" data-delay="50"
                                           data-tooltip="LinkedIn" target="_blank"
                                           :href="data.personal.linkedin">
                                            <i class="orange-text fa fa-linkedin" aria-hidden="true"></i>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="row" id="education">
                    <div class="col s12">
                        <div class="card blue-grey darken-1">
                            <div class="card-content white-text">
                                <span class="card-title orange-text"><h4><i class="fa fa-book" aria-hidden="true"></i> Education</h4></span>
                                <div class="row">
                                    <div class="col s12 m6 l6" v-for="education in data.education">
                                        <h5>{{education.name}}</h5>
                                        <p v-for="address in education.address">{{address}}</p>
                                        <p><b>{{education.year}}</b></p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="row" id="qualifications">
                    <div class="col s12">
                        <div class="card blue-grey darken-1">
                            <div class="card-content white-text">
                                <span class="card-title orange-text"><h4><i class="fa fa-graduation-cap"
                                                                            aria-hidden="true"></i> Qualifications</h4></span>
                                <div class="row">
                                    <div class="col s12 m6 l6" v-for="qualification in data.qualifications">
                                        <h5>{{qualification.name}}</h5>
                                        <p>{{qualification.subject}}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="row" id="key-experience">
                    <div class="col s12">
                        <div class="card blue-grey darken-1">
                            <div class="card-content white-text">
                                <span class="card-title orange-text">
                                    <h4>
                                        <i class="fa fa-desktop" aria-hidden="true"></i> Key Experience
                                    </h4>
                                </span>
                                <div class="row">
                                    <div class="col s12 m12 l12">
                                        <ul class="collection">
                                            <li class="collection-item blue-grey white-text"
                                                v-for="keyExperience in data.keyExperience">{{keyExperience}}
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="row" id="key-skills">
                    <div class="col s12">
                        <div class="card blue-grey darken-1">
                            <div class="card-content white-text">
                                <span class="card-title orange-text"><h4><i class="fa fa-laptop" aria-hidden="true"></i> Key Skills</h4></span>
                                <div class="row">
                                    <div class="col s12 m6 l6" v-for="keySkill in data.keySkills">
                                        <h5>{{keySkill.heading}}</h5>
                                        <table class="bordered">
                                            <tbody>
                                            <tr v-for="skill in keySkill.skills">
                                                <td>{{skill.name}}</td>
                                                <td>{{skill.time}}</td>
                                            </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="row" id="employment-history">
                    <div class="col s12">
                        <div class="card blue-grey darken-1">
                            <div class="card-content white-text">
                                <span class="card-title orange-text">
                                    <h4>
                                        <i class="fa fa-briefcase" aria-hidden="true"></i> Employment History
                                    </h4>
                                </span>
                                <div class="row">
                                    <div class="col s12 m12 l6" v-for="employmentHistory in data.employmentHistory">
                                        <div class="card-panel blue-grey lighten-2 hoverable">
                                            <h5 class="orange-text text-lighten-2">
                                                <b>{{employmentHistory.jobTitle}}</b> - {{employmentHistory.company}}
                                            </h5>
                                            <p class="time">{{employmentHistory.time}}</p>
                                            <p>{{employmentHistory.location}}</p>

                                            <p class="title">Roles and Responsibilities</p>
                                            <ul>
                                                <li v-for="responsibility in employmentHistory.responsibilities">{{responsibility}}</li>
                                            </ul>
                                            <p class="title">Key Skills</p>
                                            <div class="chip hoverable" v-for="skill in employmentHistory.keySkills">{{skill}}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

    <div class="container" v-else>
        <div class="row">
            <div class="col s12">
                <div class="card blue-grey darken-1">
                    <div class="card-content white-text">
                        <span class="card-title orange-text center-align">
                            <p class="lead">Error loading CV data.</p>
                        </span>

                        <div class="row center-align">
                            <div class="col s12 m12 l12">
                                <p>Do you have permission to view? If not, <a class="text-orange"
                                                                              href="mailto:philwc@gmail.com?subject=Request%20CV%20Access">request access now</a>
                                </p>
                                <p>If you do, please ensure the URL is correct (including the code).</p>
                                <p>Alternatively, try reloading.</p>

                                <form v-on:submit="fetchData">
                                    <div class="row">
                                        <div class="input-field inline">
                                            <i class="fa fa-terminal prefix"></i>
                                            <input id="code" class="invalid validate" type="text" v-model="code">
                                            <label for="code">Code</label>
                                        </div>

                                        <button class="waves-effect waves-light orange btn" type="submit">
                                            <i class="fa fa-refresh"></i> Reload
                                        </button>
                                    </div>
                                </form>

                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    </div>
</template>

<script>
  export default {
    data () {
      return {
        name: 'CV',
        data: false,
        code: ''
      }
    },
    created () {
      // fetch the data when the view is created and the data is
      // already being observed
      this.code = this.$route.query.code || ''
      this.fetchData()
    },
    methods: {
      fetchData () {
        let dataLocation = (process.env.DATA_SERVER || '') + '/data?code=' + this.code

        this.$http.get(dataLocation).then(
          function (response) {
            // get body data
            this.data = response.body
          },
          function (response) {
            // error callback
          }
        )
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

    @media screen {
        #employment-history {
            li {
                list-style-type: square;
                list-style-position: outside;
                margin-left: 20px;
            }

            .card-panel {
                height: 30em;
            }
        }

        p.time {
            font-style: italic;
        }

        p.title {
            font-weight: bold;
            margin: 10px 0 !important;
        }

        #personal-details {
            a {
                color: white;
                text-decoration: underline;
            }

            .card-action {
                font-weight: bold;
                a {
                    text-decoration: none;
                }
            }
        }

        /* label color */
        .input-field label {
            color: #ff9800;
        }
        /* label focus color */
        .input-field input[type=text]:focus + label {
            color: #ff9800;
        }
        /* label underline focus color */
        .input-field input[type=text]:focus {
            border-bottom: 1px solid #ff9800;
            box-shadow: 0 1px 0 0 #ff9800;
        }
        /* valid color */
        .input-field input[type=text].valid {
            border-bottom: 1px solid #ff9800;
            box-shadow: 0 1px 0 0 #ff9800;
        }
        /* invalid color */
        .input-field input[type=text].invalid {
            border-bottom: 1px solid #ff9800;
            box-shadow: 0 1px 0 0 #ff9800;
        }
        /* icon prefix focus color */
        .input-field .prefix.active {
            color: #ff9800;
        }

    }
</style>




